import React, { useState } from 'react';
import {Input, InputNumber,Form, Radio, Select,Row,Col, Button, Table, Modal, Popconfirm } from 'antd';
import './index.scss'
import { Request } from '../../component/service/axios-service';
import { PlusOutlined } from '@ant-design/icons'
const { Column } = Table;
class List extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      'list':[]
    }
    Request('GET','/ajax/syspara').then((response)=>{
      const {data}=response.data;
      console.log('mydata1',data);
      this.setState({
        'list':data
      });
      console.log('mydata2',this.state.list);
    });
  }

  renderOption  = (text,record,index) => {
    return(
      <Row>
        <Col>
          <Button type="link" onClick={()=>{this.onEditParam(index)}}>
              
            </Button>
        </Col>
        <Col>
        <Popconfirm  
          title=""
          okText=""
          cancelText=""
          onConfirm={()=>{
          console.log("text: ",text);console.log("record: ",record);console.log("index: ",index);
          this.onDeleteParam(index);
        }}>
          <Button type="link"></Button>
          </Popconfirm>
        </Col>
      </Row>
      
      );
  }


  validHintname = (rule, value, callback,index) => {
    const list = this.state.list;
    for(let i = 0;i<list.length;++i) {
      if(i!=index && list[i].hintname === value) {
        callback('')
      }
    }
    callback(); // 
  }

  validKeyword = (rule, value, callback,index) => {
    const list = this.state.list;
    for(let i = 0;i<list.length;++i) {
      if(i!=index && list[i].keyword === value) {
        callback('')
      }
    }
    callback(); // 
  }

  onAdd = (e,callback) => {
    let list=[...this.state.list];
    list.push(e);
    
    Request('POST','/ajax/syspara/addpara',JSON.stringify(e)).then((response)=>{
      this.setState({
        'list':list,
      });
      console.log('addpara',response);
    });
    callback();
  }
  
  onDeleteParam = (index)=> {
    console.log('delete');
    Request('POST','/ajax/syspara/deletepara/'+this.state.list[index].sysparaid).then((response)=>{
      console.log('addpara',response);
      window.location.reload();
    });
  }
  onAddParam = ()=>{
    const modal = Modal.confirm();
    const destroy = ()=>{
      modal.destroy();
    }
    modal.update({
      title:'',
      okText:'',
      cancelText:'',
      destroyOnClose:true,
      okButtonProps:{style:{'display':'none'}},
      cancelButtonProps:{style:{'display':'none'}},
      footer:null,
      content: (
        <Form
          onFinish={(e)=>this.onAdd(e,destroy)}
        >
          <p></p>
          <Form.Item name="hintname" rules={[{ required: true, message: '' },{validator:(rule, value, callback)=>this.validHintname(rule, value, callback,-1)}]}> 
            <Input/>
          </Form.Item>
          <p></p>
          <Form.Item name="keyword" rules={[{ required: true, message: '' },{validator:(rule, value, callback)=>this.validKeyword(rule, value, callback,-1)}]}> 
            <Input />
          </Form.Item>
          <p></p>
          <Form.Item name="value" rules={[{ required: true, message: '' }]}> 
            <Input />
          </Form.Item>
          <Button type="primary" onClick={()=>{}} htmlType="submit"></Button>
          <Button type="danger" onClick={()=>{modal.destroy()}}></Button>
        </Form>
      )
    });
  }

  onEdit = (e,index,callback) => {
    let list=[...this.state.list];
    list[index]=e;    
    Request('POST','/ajax/syspara/updatepara',JSON.stringify(e)).then((response)=>{
      this.setState({
        'list':list,
      });
      console.log('edit',response);
    });
    callback();
  }
  
  onEditParam = (index)=>{
    const modal = Modal.confirm();
    const data = this.state.list[index];
    const destroy = ()=>{
      modal.destroy();
    }
    modal.update({
      title:'',
      okText:'',
      cancelText:'',
      destroyOnClose:true,
      okButtonProps:{style:{'display':'none'}},
      cancelButtonProps:{style:{'display':'none'}},
      footer:null,
      content: (
        <Form
          onFinish={(e)=>this.onEdit(e,index,destroy)}
        >
          <p></p>
          <Form.Item name="hintname" initialValue={data.hintname} rules={[{ required: true, message: '' },{validator:(rule, value, callback)=>this.validHintname(rule, value, callback,index)}]}> 
            <Input/>
          </Form.Item>
          <p></p>
          <Form.Item name="keyword" initialValue={data.keyword} rules={[{ required: true, message: '' },{validator:(rule, value, callback)=>this.validHintname(rule, value, callback,index)}]}> 
            <Input />
          </Form.Item>
          <p></p>
          <Form.Item name="value" initialValue={data.value} rules={[{ required: true, message: '' }]}> 
            <Input />
          </Form.Item>
          <Button type="primary" onClick={()=>{}} htmlType="submit"></Button>
          <Button type="danger" onClick={()=>{modal.destroy()}}></Button>
        </Form>
      )
    });
  }


  render(){
    return (
      <Row className='system-param-contains'>
          <p className="system-param-title"><strong></strong></p>
          <Button type="primary" onClick={this.onAddParam} style={{right:'24px'}}><PlusOutlined /></Button>
          <Table  bordered pagination={false} className="system-param-table" dataSource={this.state.list} >
            <Column title="" key="name" dataIndex="hintname" />
            <Column title="" key="keyword" dataIndex="keyword" />
            <Column title="" key="value" dataIndex="value"/>
            <Column dataIndex="option" width={200} key="option" render={this.renderOption} />
          </Table>       
      </Row>
  );
  }
  
}

export default List;
