import React  from 'react' ;
import LoginLayout from '../../../component/login-component/login-layout';
import './index.scss'
import { Row , Input , Button , Form, Col,Spin} from 'antd'
import { UserOutlined , LockOutlined , MailOutlined , MobileOutlined ,LoadingOutlined, LockFilled } from '@ant-design/icons'
import { errorModal, Request, successMessage } from '../../../component/service/axios-service';

const { Item } = Form;

class SignUp extends  React.Component {
  constructor(props) {
    super(props);
    this.state=({
      'LoginType':'password',
      'tel':'',
      'second':0
    });
  }
  onFinish = (values) =>  {
    console.log('Success:',values);

    Request('POST','/ajax/signup',JSON.stringify(values)).then((response)=>{
      const {data}=response;
      console.log("Signup respond:",data);
      if(data.success) {
        successMessage('');
        window.location.href='/login';
      } else  {
       errorModal('',data.msg);
      }
    });
  }
  onFinishFail = (values) => {
    console.log('Fail',values);
  }

  handleChange = (e)=> {
    this.setState({
      'tel':e.target.value
    });
  }

  onCount = (time) => {
    let end_time = new Date().getTime() + time*1000;
    var sys_second = (end_time - new Date().getTime());
    this.timer = setInterval(()=> {
      if(sys_second>=1000) {
        sys_second -= 1000;
        this.setState({
          'second':Math.floor(sys_second/1000 % 60)
        })
      } else {
        clearInterval(this.timer);
      }
    },1000);
  }

  sendSms = () => {
    if(this.state.tel==='') {
      errorModal('','');
      return;
    } 
    Request('POST','/ajax/signupsendSms/'+this.state.tel,JSON.stringify({})).then((response)=>{
      const {data}=response;
      if(data.success) {
        this.onCount(60);
        successMessage('');
      } else {
        errorModal('',data.msg);
      }
      console.log(data);
    })
  }
  render() {
    return (
      <LoginLayout>
      <Row>
        <Form 
          name="login" 
          className="form"
          onFinish={this.onFinish}
          onFinishFailed={this.onFinishFail}
        >
          <Item name="userName" rules={[{ required: true, message: '' }]}>
            <Input className="half-opacity" size="large"  allowClear placeholder="" prefix={<UserOutlined />}/>
          </Item>
          <Item name="tel" rules={[
            { required: true, message: '' },
            { pattern: /^1[3|4|5|7|8][0-9]\d{8}$/,message: ''}
            ]} >
              <Input value={this.state.tel} onChange={this.handleChange} className="half-opacity" size="large" allowClear placeholder="" prefix={<MobileOutlined />}/>
          </Item>
          <Item>
                <Row>
                  <Col span={12}>
                    <Item name="code"  rules={[{ required: true, message: '' }]} >
                      <Input className="half-opacity" size="large" allowClear placeholder="" prefix={<MobileOutlined />}/>
                    </Item> 
                  </Col>
                  <Col span={6}>
                  {
                        this.state.second == 0?
                        <Button className="captcha-button" onClick={this.sendSms}   size="large"></Button>
                        :
                        <Button disabled   size="large"><Spin indicator={<LoadingOutlined /> }>{this.state.second}</Spin></Button>
                  }
                  </Col>
                </Row>
              </Item>
          <Item name="userPassward" hasFeedback rules={[{ required: true, message: '' }]}>
            <Input.Password className="half-opacity" size="large"  allowClear placeholder="" prefix={<LockOutlined/>}/>
          </Item>
          <Item name="confirm-password"
           dependencies={['userPassward']}
           hasFeedback
            rules={[
              { 
                required: true, message: '' 
                },
                ({getFieldValue}) =>({
                  validator(_,value) {
                    if (!value || getFieldValue('userPassward') === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error(''));
                  }
                })
                ]}>
            <Input.Password className="half-opacity" size="large"  allowClear placeholder="" prefix={<LockFilled/>}/>
          </Item>
          <Item>
            <Button className="sign-up" htmlType="submit" size="middle"></Button>
            <Button className="to-login" href="/login"  size="middle"></Button>
          </Item>
          <Item>
          </Item>
        </Form>
      </Row>
    </LoginLayout>
  );
  }
  
}

export default SignUp;
