import React  from 'react' ;
import MyLayout from '../../../component/my-layout';
import { Upload,Space,Avatar,Menu,Switch, Layout,Input,Select,Tag,Row,Col,Button,Popconfirm,Modal,Form } from 'antd'
import { getLocalData, Request } from '../../../component/service/axios-service';
import BaseList from '../../../component/base-list';
import { getDictationbyCode } from '../../../component/service/direction-service';

const STATE=[<Tag color="error"></Tag>,<Tag color="success"></Tag>,<Tag color="default"></Tag>];
const JONIABLE =[<Tag color="error"></Tag>,<Tag color="success"></Tag>]
const MODEURL = {'join':'joinlist/','create':'createlist/','all':'getallcourse'};

class ClassList extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      'list':[],
      'majorlist':[],
      'user':getLocalData('user')
    }
    Request('GET','/ajax/joinlist/'+getLocalData('user').userId).then((response)=> {
      const {data} = response.data;
      this.setState({
        'list':data || [],
        'current':'join'
      });
      console.log('all user',this.state.list);
    })
  }
  onChagneMode =(e) => {
    console.log(e);
    let url='/ajax/'+MODEURL[e.key];
    if(e.key!='all') {
      url+=getLocalData('user').userId;
    }
    Request('GET',url).then((response)=>{
      const {data}=response.data;
      this.setState({
        'current':e.key,
        'list':data || []
      });
    })
  }
  getMajor = (schoolKey)=>{
    getDictationbyCode('school').map((i)=>{
      console.log(i);
      if(i.itemKey==schoolKey) {
        Request('GET','/ajax/dictionary/dictionarydetailbypid/'+i.dictionaryDetailId).then((response)=>{
          const {data}=response.data;     
          this.setState({
            'majorlist':data,
          });
          console.log(this.state.majorlist);
        });
      }
    })
  }
  onAdd(e,callback) {
    console.log(e);
    e.joinable=e.joinable?1:0;
    e.isschoolclass=e.isschoolclass?1:0;
    Request('POST','/ajax/creatclass',JSON.stringify(e)).then((response)=>{
      console.log(response);
    })
    callback();
  }
  customRequest=(option)=> {
    const formData = new FormData();
    formData.append("files[]", option.file);
    const reader = new FileReader();
    reader.readAsDataURL(option.file);
    reader.onloadend = function(e) {
      console.log(e.target.result);// base64
      if (e && e.target && e.target.result) {
        option.onSuccess();
      }
    };
    return e.target.result;
  }

  renderButton = () => {
    return(
        <Button.Group>
            <Button onClick={()=>{window.location.href="edit-class"}} type="primary"></Button>
            <Button type="danger" style={{margin:'0px 24px'}}></Button>
        </Button.Group>
    )
}

  selectedCallback(data){
    this.setState({
      'selectedRowKey':data
    })
  }

  renderOption  = (e) => {
    
    return(
      <Row>
        <Col>
          <Button type="link">
              
            </Button>
        </Col>
        <Col>
        <Popconfirm  
          title=""
          okText=""
          cancelText=""
          onConfirm={()=>{Request("POST",'/ajax/deleteclass/'+e.courseId).then((response)=>{
            const {data}=response;
            console.log('delete',response);
            if(data.success) {
              let list=[...this.state.list];
              list.splice(e.key,1);
              this.setState({
                "list":list
              });
            }
          })}}>
            <Button type="link"></Button>
          </Popconfirm>
        </Col>
      </Row>
      );
  }

  render() {
    const {current}=this.state;
    const columns = [{
      title:'',key:'courseId',dataIndex:'courseId'
  },{
      title:'',key:'courseName',dataIndex:'courseName',
      render:(name)=>{return <Button type="link">{name}</Button>}
  },{
      title:'',key:'teacherName',dataIndex:'teacherName'
  },{
      title:'',key:'school',dataIndex:'courseschool',
  },{
      title:'',key:'major',dataIndex:'coursemajor',
  },{
      title:'',key:'term',dataIndex:'term',
  },{
      title:'',key:'joinable',dataIndex:'joinable',
      render:(status)=>{return JONIABLE[status]}
  },{
      title:'',key:'coursestate',dataIndex:'coursestate',
      render:(status)=>{return STATE[status]}
  },{
      title:'',key:'options',
      render:(e)=>{return this.renderOption(e)}
  }];

    return (
    <MyLayout>
      <Layout>
        <Menu mode="horizontal" onClick={this.onChagneMode} selectedKeys={[current]}>
          <Menu.Item key="join">
            
          </Menu.Item>
          <Menu.Item key="create">
            
          </Menu.Item>
          <Menu.Item key="all">
            
          </Menu.Item>
        </Menu>
        <Row style={{backgroundColor:"white"}}>
          {
            this.state.current == "all"?
            <Col style={{marginLeft:'auto'}}>
              {this.renderButton()}
            </Col>
            :null
          }
        </Row>
        <Row>
          <BaseList list={this.state.list} columns={columns} isSelection={true}  />
        </Row>
      </Layout>
    </MyLayout>
  );
  }
}

export default ClassList;
